<template>
    <div class="centerBox">
        <swiper :effect="'coverflow'" allowTouchMove :grabCursor="true" :centeredSlides="true" :slidesPerView="'auto'"
            :coverflowEffect="{
                rotate: 50,
                stretch: 0,
                depth: 100,
                modifier: 1,
                slideShadows: true,
                activeIndex: activeIndex
            }" :pagination="false" :modules="modules" @activeIndexChange="changeIndex" class="mySwiper">
            <swiper-slide v-for="(item, index) in list" :key="index"><img :src="item"
                    @click="goDetail(index + 1)" /></swiper-slide>
        </swiper>
    </div>
</template>

<script setup>
import { ref, defineEmits } from "vue"
import { Swiper, SwiperSlide } from 'swiper/vue';
// import { useRouter } from "vue-router"
import 'swiper/css';
import 'swiper/css/effect-coverflow';
import 'swiper/css/pagination';
import img1 from '@/assets/home/img1.png';
import img2 from '@/assets/home/img2.png';
import img3 from '@/assets/home/img3.png';
import img4 from '@/assets/home/img4.png';
import img5 from '@/assets/home/img5.png';
import img6 from '@/assets/home/img6.png';
import img7 from '@/assets/home/img7.png';
import img8 from '@/assets/home/img8.png';
import img9 from '@/assets/home/img9.png';
import img10 from '@/assets/home/img10.png';
import img11 from '@/assets/home/img11.png';
import img12 from '@/assets/home/img12.png';
import img13 from '@/assets/home/img13.png';
import { EffectCoverflow, Pagination } from 'swiper/modules';

const modules = [EffectCoverflow, Pagination];
// const router = useRouter();
const activeIndex = ref(0);
const list = ref([img1, img2, img3, img4, img5, img6, img7, img8, img9, img10, img11, img12, img13]);
const emits = defineEmits(['choseType'])

const changeIndex = (e) => {
    localStorage.setItem('swiper', e.activeIndex)
}

const goDetail = (e) => {
    emits('choseType', e)
    // router.push({
    //     name: "second",
    //     query: {
    //         id: e
    //     }
    // })
}
</script>

