const changePage = (e) => {
    if (e == 1) {
        list.value = list1[0]
        nowpage.value = e
    } else if (e == 2) {
        list.value = list1[1]
        nowpage.value = e
    } else if (e == 3) {
        list.value = list1[2]
        nowpage.value = e
    }

    if (e == "＜" && nowpage.value != 1) {
        nowpage.value--
        list.value = list1[nowpage.value - 1]
    } else if (e == "＞" && nowpage.value != 3) {
        nowpage.value++
        list.value = list1[nowpage.value - 1]
    }
